import {
  Component,
  Prop,
  Event,
  EventEmitter,
  h,
  Host,
  Listen,
} from '@stencil/core';

export type TabsType = 'underline' | 'contained';
export type TabsOrientation = 'horizontal' | 'vertical';

/**
 * @slot - Tab items (bh-tab-item components)
 */
@Component({
  tag: 'bh-tabs',
  styleUrl: 'bh-tabs.css',
  shadow: true,
})
export class BhTabs {
  /**
   * The type of tabs: underline or contained style
   */
  @Prop() type: TabsType = 'contained';

  /**
   * The orientation of the tabs: horizontal or vertical
   */
  @Prop() orientation: TabsOrientation = 'horizontal';

  /**
   * Whether the tabs should take up the full width of the container
   */
  @Prop() fullWidth: boolean = false;

  /**
   * The value of the currently active tab
   */
  @Prop({ mutable: true }) value: string = '';

  /**
   * Emitted when the active tab changes
   */
  @Event({ bubbles: true, composed: true }) bhTabChange!: EventEmitter<string>;

  @Listen('bhTabItemClick')
  handleTabItemClick(event: CustomEvent<string>): void {
    const value = event.detail;
    if (this.value !== value) {
      this.value = value;
      this.bhTabChange.emit(value);
    }
  }

  render() {
    const hostClasses = {
      tabs: true,
      [`tabs-${this.type}`]: true,
      [`tabs-${this.orientation}`]: true,
      'tabs-full-width': this.fullWidth,
    };

    return (
      <Host class={hostClasses}>
        <div class="tabs-container" part="container">
          <div
            class="tabs-list"
            part="list"
            role="tablist"
            aria-orientation={this.orientation}
          >
            <slot />
          </div>
        </div>
      </Host>
    );
  }
}
